import React from 'react';
import s from '../addcounter.module.scss';


const Addcounterblock = (props) =>{
    let name_input = React.createRef();
    let domen_input = React.createRef();

    let Addcounter = () =>{
       if(props.counterName !=='' && props.counterDomen !==''){
           props.addCounter();
       }
    };

    let onNameInputChange = () =>{
        props.reloadCounterInput(name_input.current.value,'counterNameInput')
    }
    let onDomenInputChange = () =>{
        props.reloadCounterInput(domen_input.current.value,'counterDomenInput')
    }



    return (
      <div className="row">
          <div className="col-6"><p>  </p></div>
          <div className="col-6">
              <div className={s.addcounter_form}>
                  <input onChange={onNameInputChange} type="text" value={props.counterName} placeholder=' ' ref={name_input}/>
                  <input onChange={onDomenInputChange} type="text" value={props.counterDomen} placeholder=' ' ref={domen_input}/>
                  <button className='control_button' onClick={Addcounter}> </button>
              </div>

          </div>
      </div>
    );
}
export default Addcounterblock;